<template>
  <pre>{{certidao}}</pre>
</template>

<script>
  import {WebClientI2br} from "../http";

  export default {
    name: "BoletoAnuidade",
    data: () => ({
      certidao: null,
    }),
    methods: {
      async loadData() {
        const webClient = new WebClientI2br(this.$store.getters.cpf, this.$store.getters.regional);
        const certidao = await webClient.getCertidaoInfo();
        this.certidao = certidao;
      },
    },
    created() {
      this.loadData();
    }
  }
</script>

